<template>
  <div class="feelist">
    <v-dialog v-model="dialog" width="90%">
      <template v-slot:activator="{ on }">
        <v-icon color="brown" dark large v-on="on" @click="enterProcess">mdi-cog-box</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <v-row>
              <v-col cols="12">
                <v-row>
                  <v-col cols="5">
                    <v-text-field
                      prepend-icon="mdi-cash-usd-outline"
                      label="費用名稱 or 搜尋"
                      v-model="list.name"
                      :rules="emptyRules"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="3">
                    <!-- <v-text-field label="種類" v-model="list.kind" :rules="emptyRules"></v-text-field> -->
                    <v-combobox :items="kindItems" label="種類" v-model="list.kind" :rules="emptyRules"></v-combobox>
                  </v-col>
                  <v-col cols="2">
                    <v-text-field label="單位" v-model="list.unit" :rules="emptyRules"></v-text-field>
                  </v-col>
                  <v-col cols="2">
                    <v-text-field label="費用" v-model="list.price" :rules="emptyRules"></v-text-field>
                  </v-col>
                </v-row>
              </v-col>
              <v-col cols="12">
                <v-btn class="primary" block @click="addOK" v-show="!editMode">確認新增</v-btn>
                <v-btn class="success" block @click="editOK" v-show="editMode">確認修改</v-btn>
                <v-btn class="error mt-1" block @click="cancelEditMode" v-show="editMode">取消修改</v-btn>
              </v-col>
            </v-row>
          </v-form>

          <v-card class="my-2" v-show="!editMode">
            <v-card-text>
              <v-sheet color="green lighten-3" outlined class="text-center pa-2">
                <h3 class="white--text">已建構費用列表(點選可修改)</h3>
              </v-sheet>
              <v-list class="success font-weight-black">
                <v-list-item-group>
                  <v-list-item>
                    <v-list-item-content>
                      <v-row>
                        <v-col cols="5">
                          <v-list-item-title>名稱</v-list-item-title>
                        </v-col>
                        <v-col cols="3">
                          <v-list-item-title>種類</v-list-item-title>
                        </v-col>
                        <v-col cols="2">
                          <v-list-item-title>單位</v-list-item-title>
                        </v-col>
                        <v-col cols="2">
                          <v-list-item-title>費用</v-list-item-title>
                        </v-col>
                      </v-row>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
              <v-list>
                <v-list-item-group>
                  <v-list-item v-for="(item, i) in filterItems" :key="i">
                    <v-list-item-content @click.stop="enterEditMode(item)">
                      <v-row>
                        <v-col cols="5">
                          <v-list-item-title v-text="item.name"></v-list-item-title>
                        </v-col>
                        <v-col cols="3">
                          <v-list-item-title>{{item.kind}}</v-list-item-title>
                        </v-col>
                        <v-col cols="2">
                          <v-list-item-title>{{item.unit}}</v-list-item-title>
                        </v-col>
                        <v-col cols="2">
                          <v-list-item-title>${{item.price}}</v-list-item-title>
                        </v-col>
                      </v-row>
                    </v-list-item-content>
                    <!-- <v-icon color="green" @click.stop="enterEditMode(item)">mdi-pencil</v-icon> -->
                    <v-icon color="red" @click.stop="del(item)">mdi-close</v-icon>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-card-text>
          </v-card>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      items: [],
      list: {
        name: ""
      }, //存放表單資料
      dialog: false,
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      editMode: false,
      kindItems:["固定費類","車資類","其他類","耗材類-牛奶","耗材類-技術","特別約定事項-1","特別約定事項-2","耗材類-醫療","耗材類","耗材類-約束","耗材類-管路","健檢類","醫療類","耗材類-藥品"]
    };
  },

  computed: {
    filterItems() {
      var str = "";
      return this.items.filter(item => {
        str = JSON.stringify(item).toUpperCase();
        if (str.includes(this.list.name.toUpperCase())) {
          return item;
        }
      });
    }
  },

  mounted() {
    this.getAllData();
  },

  methods: {
    getAllData() {
      //取得所有病歷資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        .then(rs => {
          this.items = rs.data;
          this.sortBy();
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    sortBy() {
      //依snkey排序
      this.items = this.items.sort(function(a, b) {
        return a.kind < b.kind ? 1 : -1;
      });
    },
    enterEdit(item) {
      //進入修改作業 -> 顯示修改頁面
      this.$refs.enterEdit.$emit("childFn", item);
    },
    enterProcess() {
      //進入畫面時,執行的是新增作業
      this.title = "相關費用資料管理";
      this.titleStyle = "font-weight-black primary lighten-2";
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        //判斷重覆
        // var repeat = this.items.filter(item => {
        //   if (this.list.name == item.name) {
        //     return item;
        //   }
        // });
        // if (repeat.length > 0) {
        //   var pop = { msg: "不可重覆!!", type: true, theme: "warning" };
        //   this.$store.commit("snackbar", pop);
        //   return;
        // }

        this.$store.commit("getTime"); //取得現在時間
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/fee",
            qs.stringify(this.list)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.getAllData();
              this.$emit("getFeeData"); //重新讀入資料
              this.list.name = "";
              this.$refs.form.resetValidation();
            }
          });
      }
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey:delData.snkey,
          tablename:'fee',
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/fee",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
                this.$emit("getFeeData"); //重新讀入資料
              }
            });
        }
      });
    },
    // del(delData) {
    //   //刪除功能
    //   this.$confirm("確認刪除?").then(res => {
    //     if (res) {
    //       this.$store.commit("getTime"); //取得現在時間
    //       var postData = {
    //         snkey: delData.snkey,
    //         info:
    //           this.$store.state.pData.username +
    //           "(" +
    //           this.$store.state.time +
    //           ")(" +
    //           delData.snkey +
    //           ")(" +
    //           delData.text +
    //           ")"
    //         //刪除的人(刪除時間)(刪除的snkey)(刪除的name / branch)
    //       };
    //       this.axios
    //         .post(
    //           "general/del/" + this.$store.state.databaseName + "/fee",
    //           qs.stringify(postData)
    //         )
    //         .then(rs => {
    //           // console.log(rs.data);
    //           if (rs.data.state == 1) {
    //             var pop = { msg: "已刪除", type: true, theme: "success" };
    //             this.$store.commit("snackbar", pop);
    //             this.getAllData();
    //             this.$emit("getFeeData"); //重新讀入資料
    //           }
    //         });
    //     }
    //   });
    // },
    enterEditMode(item) {
      //進入修改模式
      this.list = item;
      this.editMode = true;
    },
    editOK() {
      //確認修改
      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/fee",
          qs.stringify(this.list)
        )
        .then(rs => {
          if (rs.data.state == 1) {
            var pop = { msg: "已修改", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.getAllData();
            this.$emit("getFeeData"); //重新讀入資料
            this.list = {
              name: ""
            };
            this.$refs.form.resetValidation();
            this.editMode = false;
          }
        });
    },
    cancelEditMode() {
      //取消修改
      this.editMode = false;
      this.list = {
        name: ""
      };
      this.$refs.form.resetValidation();
    }
  }
};
</script>

<style lang="scss">
// .feelist{
//   font-family: '微軟正黑體';
// }
</style>